}
.advantages__title {
  margin-bottom: 16px;
}
.advantages__text {
  font-weight: 300;
  font-size: 14px;
  line-height: 17px;
}

.subscribe__bg::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(248, 248, 248, 0.7);
}
@media (max-width: 1023px) {
  .subscribe__bg .bg-img img {
    object-position: 26%;
  }
}
.subscribe__cols {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
  color: #222224;
}
@media (max-width: 1023px) {
  .subscribe__cols {
    flex-direction: column;
    gap: 0;
  }
}
.subscribe__col {
  flex: 1 1 50%;
}
@media (max-width: 767px) {
  .subscribe__col {
    flex: auto;
    width: 100%;
  }
}
.subscribe__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 167.2%;
  text-transform: uppercase;
  padding-top: 55px;
}
.subscribe__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 37px;
}
.subscribe__subtitle {
  font-size: 18px;
  margin-bottom: 32px;
}
@media (max-width: 767px) {
  .subscribe__subtitle {
    font-size: 14px;
    line-height: 22px;
    text-align: center;
  }
}
.subscribe__btn {
  background-color: #f16d7f;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  font-size: 14px;
  line-height: 17px;
  padding: 15px 19px;
}

.review {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.review__avatar {
  display: inline-block;
  width: 96px;
  height: 96px;
  border-radius: 50%;
  overflow: hidden;
  margin-bottom: 30px;
}
.review__avatar img {
  transform: scale(1.5);
  object-position: center 10px;
}
.review__title {
  font-weight: 400;
  text-align: center;
  max-width: 360px;
  font-style: italic;
}
@media (max-width: 767px) {
  .review__title {
    font-size: 18px;
    line-height: 21px;
  }
}

.subscribe-form__field {
  display: flex;
  min-height: 49px;
}
@media (max-width: 1023px) {
  .subscribe-form__field {
    width: 100%;
  }
}
.subscribe-form__email {
  font-size: 14px;
  line-height: 17px;
  border: 0;
  border-radius: 25px;
  background-color: #e1e1e1;
  padding: 10px 22px;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
@media (max-width: 1023px) {
  .subscribe-form__email {
    width: 100%;
  }
}

.footer-bottom__row {
  display: flex;
  gap: 30px;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 767px) {
  .footer-bottom__row {
    flex-direction: column-reverse;
  }
}

.footer-social__link {
  width: 32px;
  height: 32px;
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.3s ease;
}
.footer-social__link .icon {
  color: #000000;
}
@media (hover: hover) {
  .footer-social__link:hover:not(.focus-visible) {
    background-color: #f16d7f;
  }
  .footer-social__link:hover:not(.focus-visible) .icon {
    color: #ffffff;
  }
}

